import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import router from "@/router";
router

const API_URL = 'https://server.gold-ticket.uz/api';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        agreed: null,
        lang: null,
        name: null,
        phone: null,
        photo: null,
        premium: null,
        username: null,
        tickets: null,
        tickets_count: 0,
        errors: null,
        error: null,
    },
    getters: {},
    mutations: {
        setUser(state, data){
            state.agreed = data.agreed;
            state.lang = data.lang;
            state.name = data.name;
            state.phone = data.phone;
            state.photo = data.photo;
            state.premium = data.subscription ? '' : 'active';
            state.username = data.username;
        },
        setTickets(state, data){
            state.tickets = data.data;
            state.tickets_count = data.total;
        },
        userSendInvoice(state, data) {
            if (data === true) {
                if (state.lang === 'ru') {
                    Telegram.WebApp.showPopup({
                        'title': 'Успешно',
                        'message': 'Бот вам отпрвил счёт для оплаты',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Продолжить"
                            }
                        ]
                    })
                } else {
                    Telegram.WebApp.showPopup({
                        'title': 'Muvofaqiyatli',
                        'message': 'Bot sizga tolov uchun hisob yubordi',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Davom etish"
                            }
                        ]
                    })
                }
            } else {
                if (state.lang === 'ru') {
                    Telegram.WebApp.showPopup({
                        'title': 'Ошибка!',
                        'message': 'Произошла ошибка, повторите попытку больше ',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Повторить"
                            }
                        ]
                    })
                } else {
                    Telegram.WebApp.showPopup({
                        'title': 'Xato!',
                        'message': 'Tolo hisibi yaratishda hatolik yuz berdi!\n\n Keyinroq qayta urunib koring',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Davom etish"
                            }
                        ]
                    })
                }
            }
        },
        userCreateInvoice(state, data) {
            if (data !== false) {
                window.Telegram.WebApp.openInvoice(data.result)
            } else {
                if (state.lang === 'ru') {
                    Telegram.WebApp.showPopup({
                        'title': 'Учавствовать',
                        'message': 'Произошла ошибка, повторите попытку больше ',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Повторить"
                            }
                        ]
                    })
                } else {
                    Telegram.WebApp.showPopup({
                        'title': 'Qatnashish',
                        'message': 'Tolov hisibi yaratishda hatolik yuz berdi!\n\n Keyinroq qayta urunib koring',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Davom etish"
                            }
                        ]
                    })
                }
            }
        },
        userUpdateProfile(state, data) {
            if (data === true) {
                Telegram.WebApp.MainButton.hide()
                Telegram.WebApp.MainButton.hideProgress()
                if (state.lang === 'ru') {
                    Telegram.WebApp.showPopup({
                        'title': 'Успешно',
                        'message': 'Ваши данные успешно сохранены',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Ок"
                            }
                        ]
                    })
                } else {
                    Telegram.WebApp.showPopup({
                        'title': 'Muvofaqiyatli!',
                        'message': 'Sizning malumotlaringiz muvofatqiyatli saqlandi',
                        'buttons': [
                            {
                                'id': 'success',
                                'type': 'ok',
                                'text': "Ok"
                            }
                        ]
                    })
                }
                router.push('/')
            } else {
                Telegram.WebApp.showPopup(data)
            }
        },
        userAgreedAccess(state, status) {
            if (status === true) {
                state.agreed = true;
                router.push('/')
            }
        },
        error(data) {
            if (data.errors) {
                Telegram.WebApp.showPopup = data.errors;
            } else {
                Telegram.WebApp.showPopup = data.error;
            }
        }
    },
    actions: {
        async getUser({ commit }, user_id) {
            try {
                const res = await axios.get(`${API_URL}/user/${user_id}`)
                commit("setUser", res.data.data);
            } catch (e) {
                commit("error", e.response.data);
                throw e;
            }
        },
        async updateUser({ commit }, data) {
            try {
                await axios.post(`${API_URL}/user/${data.user_id}`, data.data)
                commit("userUpdateProfile", true)
            } catch (e) {
                commit("userUpdateProfile", data)
            }
        },
        async userAgreed({ commit }, data) {
            try {
                await axios.post(`${API_URL}/user/${data.user_id}`, data.data)
                commit("userAgreedAccess", true)
            } catch (e) {
                commit("error", false)
            }
        },
        async getTickets({ commit }, user_id) {
            try {
                const res = await axios.get(`${API_URL}/ticket/${user_id}`)
                commit("setTickets", res.data)
            } catch (e) {
                commit("error", e.response.data)
            }
        },
        async sendInvoice({ commit }, user_id) {
            try {
                await axios.post(`${API_URL}/invoice/${user_id}`)
                commit("userSendInvoice", true)
            } catch (e) {
                if (!e.response.data.ok) {
                    commit("userSendInvoice", false)
                }
            }
        },
        async createInvoice({ commit }, data) {
            try {
                const res = await axios.post(`${API_URL}/invoice`, data)
                commit("userCreateInvoice", res.data)
            } catch (e) {
                commit("userCreateInvoice", false)
            }
        }
    },
})
